  animation-name: slideOutDown;
}
@-webkit-keyframes slideOutLeft {
  0% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  100% {
    visibility: hidden;
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
  }
}
@keyframes slideOutLeft {
  0% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  100% {
    visibility: hidden;
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
  }
}
.slideOutLeft {
  -webkit-animation-name: slideOutLeft;
  animation-name: slideOutLeft;
}
@-webkit-keyframes slideOutRight {
  0% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  100% {
    visibility: hidden;
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
  }
}
@keyframes slideOutRight {
  0% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  100% {
    visibility: hidden;
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
  }
}
.slideOutRight {
  -webkit-animation-name: slideOutRight;
  animation-name: slideOutRight;
}
@-webkit-keyframes slideOutUp {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    visibility: hidden;
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
  }
}
@keyframes slideOutUp {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    visibility: hidden;
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
  }
}
.slideOutUp {
  -webkit-animation-name: slideOutUp;
  animation-name: slideOutUp;
}
/* Variables
 * All project variables
*/
*,
*:after,
*:before {
  box-sizing: border-box;
}
html {
  font-size: 10px;
}
body {
  font-size: 1.6rem;
  font-family: 'Arial';
  padding-bottom: 30px;
  color: #666666;
  text-rendering: optimizeLegibility;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-style: normal;
  font-weight: 300;
}
.btn {
  border: 0;
  background-color: none;
  outline: 0;
}
.container {
  max-width: 980px;
  margin: 0 auto;
}
.anchor {
  display: none;
}
.main-nav {
  background-color: #009999;
  padding: 20px 25px;
  display: inline;
  float: left;
  width: 100%;
  margin: 0 0%;
}
.main-logo {
  display: inline;
  float: left;
  width: 25%;
  margin: 0 0%;
  max-width: 170px;
  text-align: center;
}
.main-logo i {
  display: inline-block;
  font-size: 3.6rem;
  color: #ffffff;
  margin-right: 15px;
}
.main-logo-title {
  display: inline;
  font-size: 3.6rem;
  color: #ffffff;
  margin: 0;
}
.main-search {
  display: inline;
  float: left;
  width: 33.33333333%;
  margin: 0 0%;
  position: relative;
}
.custom-search {
  position: relative;
}
.custom-search:after {
  content: "\f002";
  font-family: "FontAwesome";
  position: absolute;
  font-size: 2.6rem;
  display: inline-block;
  top: 10px;
  left: 30px;
}
.search {
  display: block;
  width: 100%;
  height: 40px;
  outline: 0;
  margin-left: 15px;
  border-radius: 12px;
  padding-left: 50px;
  padding-right: 20px;
  border: 1px solid #404040;
}
.main-menu {
  display: inline;
  float: left;
  width: 41.66666667%;
  margin: 0 0%;
  float: right;
  padding-left: 0;
  margin-top: 12px;
  text-align: center;
}
.main-menu a {
  text-decoration: none;
}
.main-menu a:last-child li {
  border-right: 0;
}
.main-menu-item {
  color: #ffffff;
  font-size: 1.8rem;
  display: inline-block;
  padding: 0 25px;
  border-right: 1px solid #ffffff;
}
.main-section {
  display: inline;
  float: left;
  width: 100%;
  margin: 0 0%;
  margin: 45px 0;
}
.main-section-title {
  font-size: 3.6rem;
  font-weight: 700;
  text-align: center;
  color: #333333;
}
.cl-one {
  display: inline-block;
  margin: 0;
}
.cl-one:first-letter {
  color: #0099cc;
}
.cl-two {
  display: inline-block;
  margin: 0;
}
.cl-two:first-letter {
  color: #993366;
}
.cl-three {
  display: inline-block;
  margin: 0;
}
.cl-three:first-letter {
  color: #666666;
}
.section-text {
  font-size: 2.2rem;
  max-width: 775px;
  margin: 0 auto;
  margin-top: 60px;
}
@media (max-width: 768px) {
  .anchor {
    display: block;
    color: #009999;
    font-size: 3.6rem;
    position: fixed;
    right: 15px;
    top: 15px;
    cursor: pointer;
    z-index: 10;
  }
  .main-nav {
    position: fixed;
    height: 100%;
    z-index: 9;
    transform: translateX(-768px);
    transition: 0.3s all ease-in-out;
  }
  .main-logo {
    display: inline;
    float: left;
    width: 100%;
    margin: 0 0%;
    max-width: 100%;
  }
  .main-search {
    display: inline;
    float: left;
    width: 100%;
    margin: 0 0%;
    max-width: 100%;
  }
  .custom-menu {
    width: 80%;
    margin: 0 auto;
  }
  .search {
    display: block;
    margin: 0 auto;
    max-width: 80%;
  }
  .custom-search {
    margin: 20px auto;
  }
  .main-menu {
    display: inline;
    float: left;
    width: 100%;
    margin: 0 0%;
  }
  .menu-active .main-nav {
    transform: translateX(0);
  }
  .menu-active .anchor {
    color: #ffffff;
  }
  .main-menu-item {
    display: block;
    border-right: 0;
    padding: 20px 0;
    border-bottom: 1px solid #ffffff;
  }
  .section-text {
    text-align: center;
  }
}
.ideb {
  display: inline;
  float: left;
  width: 100%;
  margin: 0 0%;
}
.ideb-header {
  display: inline;
  float: left;
  width: 100%;
  margin: 0 0%;
  margin: 20px 0;
}
.ideb-title {
  float: left;
  font-size: 2.2rem;
  font-weight: 700;
}
.social-list {
  float: right;
  list-style: none;
  border: 1px solid #000000;
  padding-left: 0;
}
.social-list a {
  text-decoration: none;
}
.social-item {
  display: inline-block;
  color: #000000;
  font-size: 1.2rem;
  padding: 5px 10px;
  border-right: 1px solid #000000;
}
.ideb-table {
  width: 100%;
  border: 1px solid #999999;
}
.ideb-table th {
  padding: 16px;
}
.ideb-tbody td {
  border: 1px solid #999999;
  padding: 40px 35px;
}
.ideb-city {
  text-align: center;
}
